@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
#editor {
	width: 100%;
	height: 100%;
	position: fixed;
	background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAD0AAAA8BAMAAAAnEK9/AAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAAAGUExURVFRUTMzM3nhs84AAAAJcEhZcwAADsMAAA7DAcdvqGQAAAAhSURBVEjHY6AYCOIBAqPyo/Kj8qPyo/Kj8sNUnhLAwAAAui1roY+vV8MAAAAASUVORK5CYII=);
	background-repeat: repeat;
	background-size: 25px;
}

#editor-menu {
	width: 100%;
	display: inline-block;
	background-color: #242424;
	padding-bottom: 10px;
	padding-top: 10px;
}

#editor-menu>h1 {
	color: white;
	margin: 0;
	padding: 0;
	font-size: 21px;
	position: fixed;
	top: 10px;
	right: 20px;
}

#editor-menu>div {
	display: inline-block;
	margin-left: 10px;
}

#editor-menu>div>button {
	background-color: #494949;
	color: white;
	border: none;
	padding: 5px;
	margin-right: 5px;
	width: 100px;
	cursor: pointer;
}
#node-selector-menu {
	width: 250px;
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	flex-flow: column;
	height: 100%;
	border-right: solid 1px #494949;
	position: fixed;
}

#node-selector-menu>input {
	margin: 5px;
	margin-top: 10px;
	background-color: rgba(0, 0, 0, 0.75);
	border: solid 2px rgba(0, 0, 0, 0.75);
	color: white;
	padding: 5px;
}

#node-selector-menu>input:focus {
	border: solid 2px #494949;
}

#node-selector-menu>hr {
	margin: 0;
	width: 100%;
	margin-top: 5px;
	margin-left: -2px;
	color: #494949;
}
html, body, div {
	margin: 0;
	padding: 0;
	font-family: 'Roboto', sans-serif;
}
